    methods: {
        getPosition: function() {
            return {
                lat: parseFloat(this.itinerario.acf.indirizzo.lat),
                lng: parseFloat(this.itinerario.acf.indirizzo.lng)
            }
        },
        toggleInfo: function(marker, key) {
            this.startLocation = this.getPosition()
            this.infoPosition = this.getPosition()
            this.infoContent = marker.full_name
            if (this.infoCurrentKey == key) {
                this.infoOpened = !this.infoOpened
            } else {
                this.infoOpened = true
                this.infoCurrentKey = key
            }
        },
        funLocaliOption: function(){
            return {
                slidesPerView: 4,
                spaceBetween: 30,
                loop: true,
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                },
            }
        },
        funAttivitaOption: function(){
            return {
                slidesPerView: 4,
                spaceBetween: 30,
                loop: true,
                pagination: {
                    el: '.swiper-pagination-attivita',
                    clickable: true,
                },
            }
        },
        startLocaliSwiper: function(){
            this.swiperLocaliOptions = this.funLocaliOption();
        },
        startAttivitaSwiper: function(){
            this.swiperAttivitaOptions = this.funAttivitaOption();
        }
    },
    created() {
        this.$store.dispatch("locali");
        this.$store.dispatch("attivita");
        this.$store.dispatch("itinerari");
    },
    beforeUpdate(){
        // FOR LOCALI
        var idPosts = [];
        var i;
        for (i = 0; i < this.itinerario.acf.locali_vicini.length; i++) {
            idPosts.push(this.itinerario.acf.locali_vicini[i].ID);
            console.log(this.itinerario.acf.locali_vicini[i].ID);
        }
        console.log()
        var filtered = this.$store.state.content.locali.filter((item) => idPosts.includes(item.id));
        this.correlatiLocali = filtered;
        // FOR ATTIVITA
        var relPosts = [];
        var i;
        for (i = 0; i < this.itinerario.acf.attivita_vicine.length; i++) {
            relPosts.push(this.itinerario.acf.attivita_vicine[i].ID);
        }
        var filtroattivita = this.attivita.filter((item) => relPosts.includes(item.id));
        this.correlatiAttivita = filtroattivita;
    }
